export default {
    layout: "Emp-content",
    mounted() {
        this.employeeDetails();
        
    },
    data() {
        return {
            employee: [],
            errors: {},
            
        };
    },
    methods:{
         employeeDetails() {      
      this.$axios
        .$get("/employee/details/" +this.$route.params.id)
        .then((res) => {         
            
          this.employee = res;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    }
}
